* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.country-list-item {
  list-style: none;
  display: flex;
  align-items: center;
}

.renderCountryInfo-firstString {
  display: flex;
  align-items: baseline;
  font-size: 36px;
  font-weight: bold;
}

h2 {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
  max-width: 100%;
  margin-right: 10px;
}
